import {type SafeHTMLString, SafeHTMLText} from '@github-ui/safe-html'
import {Box} from '@primer/react'
import type React from 'react'

export type CodeLine = {
  html: SafeHTMLString
  number: number
}

export const StaticSnippet = (props: {lines: CodeLine[]}) => {
  const lines: React.ReactNode[] = []

  for (const line of props.lines) {
    lines.push(
      <tr key={line.number} role="row">
        <td role="cell">
          <Box
            as="span"
            className="blob-num"
            sx={{
              display: 'block',
              textDecoration: 'none !important',
              color: 'fg.subtle',
              ':hover': {
                cursor: 'text',
              },
            }}
          >
            {line.number}
          </Box>
        </td>
        <SafeHTMLText
          as="td"
          className="blob-code blob-code-inner"
          role="cell"
          html={line.html}
          sx={{
            cursor: 'text',
            wordWrap: 'break-word',
            color: 'fg.default',
            overflowWrap: 'anywhere',
            mark: {backgroundColor: 'var(--highlight-neutral-bgColor, var(--color-search-keyword-hl))'},
          }}
        />
      </tr>,
    )
  }

  return (
    <Box
      className="code-list"
      sx={{
        whiteSpace: 'pre-wrap',
        fontFamily: 'monospace',
        minWidth: 0,
        py: 2,
      }}
    >
      <table role="table">
        {
          // We need to add role="rowgroup" because we don't have table headers
          // And in order to screen readers to know that we really mean for this to be a table
          // We must define it explicitly
          <tbody role="rowgroup">{lines}</tbody>
        }
      </table>
    </Box>
  )
}

try{ StaticSnippet.displayName ||= 'StaticSnippet' } catch {}